/* App.css */

/* Main App Style */
.App {
  text-align: center;
  padding: 20px;
  background: linear-gradient(135deg, #a5d6a7, #ffeb3b); /* Background gradient */
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: backgroundAnimation 5s ease-in-out infinite; /* Animating background gradient */
}

.greeting-container {
  margin-bottom: 30px;
}

.kangaroo-img {
  width: 300px;
  height: auto;
  transition: transform 0.3s ease;
}

.kangaroo-img:hover {
  transform: scale(1.1);
}

/* Button Style */
button {
  padding: 10px 20px;
  background-color: #ffeb3b;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #fbc02d;
}

/* Popup Styling */
.popup {
  background-color: #8f2c2c;  /* Bright yellow for the popup */
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  animation: popupAnimation 5s ease-in-out infinite; /* Animating popup background */
}

/* Animated Gradient Background for Popup */
@keyframes popupAnimation {
  0% {
    background: linear-gradient(135deg, #cc6d50, #945b19);
  }
  50% {
    background: linear-gradient(135deg, #ff7043, #a5d6a7);
  }
  100% {
    background: linear-gradient(135deg, #cc6942, #ed9a3b);
  }
}

/* Animated Gradient Background for the App */
@keyframes backgroundAnimation {
  0% {
    background: linear-gradient(135deg, #cc6d50, #945b19);
  }
  50% {
    background: linear-gradient(135deg, #ff7043, #a5d6a7);
  }
  100% {
    background: linear-gradient(135deg, #cc6942, #ed9a3b);
  }
}

/* Overlay Styling */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
}




